
body {
  font-family: monospace;
}
li {
  margin: 1em 0;
}

.hud {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}
@media screen and (min-width: 680px) {
.hud {
      display: flex;
      justify-content: space-between;
}
.hud > * {
        flex: 1 1 0;
}
}

.warning {
  color: #fa0;
}
.danger {
  color: #c00;
}

.action {
  margin: 0;
}
